
import XHeader from 'vux/src/components/x-header/index.vue'

import { mapGetters } from "vuex";
export default {
components: {
XHeader
},
computed: {
...mapGetters(["demoDetail"])
},
created() {

},
watch: {
demoDetail: function(val) {
this.details = val;
},
details: function(val) {
let htmlUtil = this.$options.filters.htmlUtil;
this.realHtml = htmlUtil(val.thumb_content);
}
},
activated() {
this.$store.dispatch("getDemoDetail", { id: this.$route.params.demoid });
},
deactivated() {

this.realHtml = ''
},
data() {
return {
details: {},

realHtml: ""
};
},
filters: {
htmlUtil: function(str) {
let s = "";
if (str.length == 0) return "";
s = str.replace(/&amp;/g, "&");
s = s.replace(/&lt;/g, "<");
s = s.replace(/&gt;/g, ">");
s = s.replace(/&nbsp;/g, " ");
s = s.replace(/&#39;/g, "'");
s = s.replace(/&quot;/g, '"');
return s;
}
}
};



